import React, { FunctionComponent } from 'react';
import { CoordinateArray } from '../classes/Coordinate';
import { PERSPECTIVE } from '../space/PERSPECTIVE';

export const DualMeshTileComponent: FunctionComponent<{
	center: CoordinateArray;
	nodes: CoordinateArray[];
}> = ({ center, nodes }) => {
	return (
		<polyline
			fill="rgba(0,0,0,0.1)"
			stroke="black"
			points={[...nodes, nodes[0]]
				.map(n => PERSPECTIVE.toPixels(n[0], n[1], 0).join(','))
				.join(' ')}
		/>
	);
};
